/**
 * 
 */
'use strict'
import '../../styles/todo/todo.css'
import React, { Component,PropTypes } from 'react';
import { connect } from 'react-redux';
import { addTodo, completeTodo, setVisibilityFilter, VisibilityFilters } from '../../actions/todo/actions';
import AddTodo from './AddTodo';
import TodoList from './TodoList';
import Footer from './Footer';
import Nav from '../nav/Nav';

class TodoApp extends Component {
    render() {
        // react-redux connect
        const { dispatch, visibleTodos, visibilityFilter } = this.props
        return (
            <div>
                <Nav />
                <AddTodo onAddClick={text => dispatch(addTodo(text))} />
                <TodoList todos={visibleTodos}
                    onTodoClick={index=> { dispatch(completeTodo(index)) } }
                    />
                <Footer  filter={visibilityFilter}  onFilterChange={filter => {
                    dispatch(setVisibilityFilter(filter))
                } } />
            </div>
        )
    }
}
TodoApp.propTypes={
    visibleTodos:PropTypes.arrayOf(PropTypes.shape({
        text:PropTypes.string.isRequired,
        completed:PropTypes.bool.isRequired
    })),
    visibilityFilter:PropTypes.oneOf([
        'SHOW_ALL',
        'SHOW_COMPLETED',
        'SHOW_ACTIVE'
    ]).isRequired
}

function selectTodos(todos,filter){
    switch(filter){
        case VisibilityFilters.SHOW_ALL:
            return todos
        case VisibilityFilters.SHOW_COMPLETED:
            return todos.filter(todo=> todo.completed)
        case VisibilityFilters.SHOW_ACTIVE:
            return todos.filter(todo=>!todo.completed)
    }
}
//  state  props ?
//  https://github.com/reactjs/reselect 
function  select(state){
    return{
        visibleTodos:selectTodos(state.todos,state.visibilityFilter),
        visibilityFilter:state.visibilityFilter
    }
}
//  component  dispatch  state  connect(select)(App) 
export default connect(select)(TodoApp)


