'use strict'
import React from 'react';
import Nav from '../nav/Nav';

class LifeCycleComponent extends React.Component{
    constructor(props){
        super(props);
        fpLog(':constructor()');
    }
    //state
    componentWillMount(){
        fpLog(':componentWillMount()')
    }
    render(){
        fpLog(':render()');
        return(
            <div>
                <Nav/>
                <p>{this.props.text}</p>
                <a href="https://facebook.github.io/react/docs/react-component.html" target="_blank">Component Specs and Lifecycle</a>
            </div>
        )
    }
    shouldComponentUpdate(nextProps, nextState){
        //truefalserender()
        //forceUpdate
        fpLog('propsstate:shouldComponentUpdate()');
        fpLog2('<span style="color:blue">props.text='+this.props.text+'</span>');
        fpLog2('<span style="color:red">props.text='+nextProps.text+'</span>');

        if(this.props.info!==nextProps.text){
            return true
        }
    }
    componentWillUpdate(){
         //propsstate
        fpLog('propsstate:componentWillUpdate()');
    }
    componentDidUpdate(){
        fpLog('propsstateDOM:componentDidUpdate()');
    }
    //DOM
    componentDidMount(){
        fpLog('DOM:componentDidMount()')
    }
    componentWillReceiveProps(nextProps){
        fpLog('props:componentWillReceiveProps()---'+nextProps.text)
    }
    componentWillUnmount(){
        fpLog('componentWillUnmount()')
    }
}
LifeCycleComponent.dispalyName='LifeCycleComponentDemo';
export default LifeCycleComponent;


